.body{
  background-color: #61dafb;
}

.App {
  background: #263f05;;
  border-radius:0 20px;
  min-height: 500px;
  width: 500px;
  margin:40px auto;
}

.todo-form-input{
  background: rgb(4, 17, 2);
  border-radius:0 20px;
  margin:4px;
  padding:5px;
  max-width: 400px;
  font-size:18px;
  width: 400px;
}

.todo-form-button{
  height:20px;
  margin: 2px;
  border:0;
  position:relative;
  border-radius: 5px;
  background-color: rgba(202,156,20);
  cursor:pointer;
}
.todo{
  background: #fff;;
  box-shadow:1 px 1px 1px rgba(0,0,0,15);
  border-radius:3px;
  padding:3px 10px;
  font-size: 12px Bold;
  margin-bottom: 6px;
  align-items:center;
  justify-content: space-between;

}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
